import { StaticImage } from "gatsby-plugin-image";
import React, { useCallback, useEffect, useRef, useState } from "react";
import peopleImage from "@styles/assets/images/hero_v2/people-image.svg";

const achievementsdatas = [
  {
    id: 0,
    counts: 35,
    title: "Happy Clients",
  },
  {
    id: 0,
    counts: 65,
    title: "Successful Projects",
  },
  {
    id: 0,
    counts: 15,
    title: "Our Experts",
  },
  {
    id: 0,
    counts: 11,
    title: "Years of Experience",
  },
];
const About = () => {
  const [achievements, setAchievements] = useState(
    achievementsdatas.map((a) => ({ ...a, count: 0 }))
  );
  const [hasStarted, setHasStarted] = useState(false);

  const achievementRef = useRef(null);

  const StartCounting = useCallback(() => {
    achievementsdatas.forEach((achievement, index) => {
      const interval = setInterval(() => {
        setAchievements((prev) => {
          const updated = [...prev];
          const current = updated[index];

          if (current.count < achievement.counts) {
            current.count += 1;
          } else {
            clearInterval(interval);
          }

          return updated;
        });
      }, 40);
    });
  }, []);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting && !hasStarted) {
          setHasStarted(true);
          StartCounting();
        }
      },
      { threshold: 0.5 }
    );

    if (achievementRef.current) {
      observer.observe(achievementRef.current);
    }

    return () => {
      if (achievementRef.current) {
        observer.unobserve(achievementRef.current);
      }
    };
  }, [hasStarted, StartCounting]);

  return (
    <section
      className="home-wrapper-v2 mt-5 item-start gap-3xl"
      ref={achievementRef}
    >
      <div className="flex-start gap-3xl md-flex-column">
        <div className="home-about-image-v2">
          <StaticImage
            src="../../styles/assets/images/hero_v2/about-image.webp"
            alt="justtry-about-image"
            fetchpriority="high"
            layout="constrained"
            quality={75}
            loading="eager"
            placeholder="none"
          />
        </div>
        <div className="home-about-v2 item-start gap-md">
          <span className="lightcolor-badge-v2 about-p flex-center gap-2">
            <img src={peopleImage} alt="people-icon" />
            About us
          </span>
          <h2 className="about-h2">Who We Are</h2>
          <p className="about-p">
            Justtry Technologies is a blockchain development company and a
            leader in digital transformation. Our mission is to provide
            businesses, developers, and enterprises with market-centric,
            cutting-edge blockchain solutions that drive innovation, security,
            and efficiency. <br />
            <br />
            With a specialist team of industry experts focused on technological
            advancement, we excel in custom blockchain solutions, smart
            contracts, DeFi, NFTs, Web3 applications, and more. Leveraging the
            latest advancements in blockchain, AI, and cloud computing, we
            enable businesses to embrace decentralization, security, and
            scalability. <br />
            <br />
            We deliver results that mattersolutions that enhance transparency,
            optimize operations, and create new revenue streams. As the
            blockchain ecosystem evolves, Justtry Technologies remains at the
            forefront, pioneering future-ready solutions that redefine
            industries.
          </p>
        </div>
      </div>

      <div className="home-v2-achievements flex-start gap-xl">
        {achievements.map((achievement) => (
          <div
            key={achievement.id}
            className="home-v2-achievement flex-start gap-md"
          >
            <span className="home-v2-achievement-box mt-3 "></span>
            <div className="item-start gap-3">
              <p className="about-h4">{achievement.count} +</p>
              <p className="about-p-two text-nowrap">{achievement.title}</p>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default About;
