import React, { useEffect, useState } from "react";
import TalkToUsNew from "@components/common/talk-to-us-new";
import { Link } from "@reach/router";
import arrowrightSide from "@styles/assets/images/hero_v2/arrow-down-right.png";
import { StaticImage } from "gatsby-plugin-image";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import { Justtrylinks } from "@components/common/justtrylinks";

const Hero = (props) => {
  const { db } = props;

  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  const slides = [
    "NFT Marketplace",
    "Web3",
    "Crypto Exchange",
    "Blockchain",
    "Game",
    "Metaverse",
    "DeFi",
    "Wallet",
    "Dapp",
  ];

  return (
    <section className="hero-wrapper-v2">
      <div className="home-wrapper-v2 item-start gap-2xl">
        <div className="item-start c-gap">
          <div>
            <h1 className="about-special font-black fw-600 flex-center gap-sm md-no-gap md-flex-column md-c-flex-start text-nowrap ">
              <span className="mr-4"> We Are </span>
              <div className="hero_banner_title ">
                <div className="hero_title_sec">
                  {isClient ? (
                    <Swiper
                      modules={[Autoplay]}
                      direction={"vertical"}
                      speed={900}
                      autoplay={{
                        delay: 1500,
                        disableOnInteraction: false,
                      }}
                      loop={true}
                      slidesPerView={1}
                      className="mySwiper"
                      breakpoints={{
                        640: {
                          slidesPerView: 1,
                        },
                        768: {
                          slidesPerView: 1,
                        },
                        1024: {
                          slidesPerView: 1,
                        },
                        1400: {
                          slidesPerView: 1,
                        },
                        1800: {
                          slidesPerView: 1,
                        },
                      }}
                    >
                      {slides.map((slide, idx) => (
                        <SwiperSlide key={idx} style={{ height: "50px" }}>
                          <div className="banner_content_sec gradient-text-bg">
                            <span>{slide}</span>
                          </div>
                        </SwiperSlide>
                      ))}
                    </Swiper>
                  ) : (
                    <div className="banner_content_sec gradient-text-bg">
                      <span>NFT Marketplace</span>
                    </div>
                  )}
                </div>
              </div>
            </h1>
            <span className="about-special font-black fw-600 ">
              Development Company
            </span>
          </div>
          <p className="about-p">
            Our Team specializes in modernizing and automating industries,
            enterprises, and Businesses with modern digital transformation
            technology solutions like blockchain, AI, Metaverse, Crypto, NFT,
            and Web3.
          </p>
          <div className="buttons mt-5">
            <Link to={Justtrylinks.contactUs} className="roll-button roll-btn">
              <span>Get Started</span>
            </Link>
            <TalkToUsNew
              db={db}
              label="Our Works"
              customClass="normal-roll-button normal-roll-btn"
            />
          </div>
        </div>

        <div className="hero-bottom-links-v2 w-100 md-flex-column justify_between gap-4">
          <a
            onClick={() =>
              document
                .getElementById("service")
                ?.scrollIntoView({ behavior: "smooth" })
            }
            className="about-p font-black gap-3 hero-bottom-link flex-center"
          >
            Our Services <img src={arrowrightSide} alt="arrow-icon" />{" "}
          </a>
          <a
            onClick={() =>
              document
                .getElementById("techstack")
                ?.scrollIntoView({ behavior: "smooth" })
            }
            className="about-p font-black gap-3 hero-bottom-link flex-center"
          >
            Our development Technologies{" "}
            <img src={arrowrightSide} alt="arrow-icon" />{" "}
          </a>
          <a
            onClick={() =>
              document
                .getElementById("projects")
                ?.scrollIntoView({ behavior: "smooth" })
            }
            className="about-p font-black gap-3 hero-bottom-link flex-center"
          >
            Our Process <img src={arrowrightSide} alt="arrow-icon" />{" "}
          </a>
        </div>
      </div>
      <div className="img-container">
        <StaticImage
          src="../../styles/assets/images/hero_v2/banner-image.webp"
          alt="justtry-banner-wave-image"
          layout="constrained"
          quality={75}
          loading="eager"
          placeholder="none"
          className="w-100"
        />
      </div>
    </section>
  );
};

export default Hero;
