import TalkToUsNew from "@components/common/talk-to-us-new";
import { Link } from "@reach/router";
import React, { useEffect, useRef } from "react";
import process from "@styles/assets/images/hero_v2/process.svg";
import { useGSAP } from "@gsap/react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const datas = [
  {
    id: 0,
    number: 1,
    title: "Ideation",
    desc: "During the ideation phase, we cultivate innovative concepts that align with your business goals and vision.",
  },
  {
    id: 1,
    number: 2,
    title: "Planning and Strategy",
    desc: "Crafting a roadmap, we strategically plan every step, ensuring a clear and effective development strategy.",
  },
  {
    id: 2,
    number: 3,
    title: "Design & Proto",
    desc: "In the design stage, our creative team transforms ideas into visually stunning prototypes for your review and approval.",
  },
  {
    id: 3,
    number: 4,
    title: "Developments",
    desc: "Our experienced developers bring the prototypes to life, coding robust and scalable solutions tailored to your requirements.",
  },
  {
    id: 4,
    number: 5,
    title: "Testing & QA",
    desc: "Rigorous testing ensures the reliability, security, and performance of the developed solutions, adhering to the highest quality standards.",
  },
  {
    id: 5,
    number: 6,
    title: "Deployments",
    desc: "We deploy the finalized product, ensuring a seamless launch and providing support during the initial rollout.",
  },
  {
    id: 6,
    number: 7,
    title: "Supports",
    desc: "Post-launch, our commitment continues with regular maintenance, updates, and responsive support to keep your solution running smoothly.",
  },
  {
    id: 7,
    number: 8,
    title: "Client Collaboration",
    desc: "Throughout the process, we maintain open communication, welcoming your feedback to refine and optimize the development journey collaboratively.",
  },
];

const Process = (props) => {
  const { db } = props;
  const container = useRef(null);
  const cardsWrapper = useRef(null);

  useGSAP(
    () => {
      const setupScrollTrigger = () => {
        ScrollTrigger.matchMedia({
          "(min-width: 1025px)": function () {
            const cards = gsap.utils.toArray(".horizontal-scroll-card");
            const progressbar = document.querySelector(".progress-bar");

            // Check if cards exist and the first card is available
            if (!cards || cards.length === 0 || !cards[0]) {
              console.warn("No cards found for horizontal scroll");
              return;
            }

            // Check if the first card has offsetWidth property
            if (
              !cards[0].offsetWidth ||
              typeof cards[0].offsetWidth !== "number"
            ) {
              console.warn(
                "Card element doesn't have offsetWidth property yet",
              );
              return;
            }

            const cardWidth = cards[0].offsetWidth;
            const totalWidth = cards.length * cardWidth - window.innerWidth;

            // Ensure totalWidth is positive
            if (totalWidth <= 0) {
              console.warn("Total width is not positive, skipping animation");
              return;
            }

            // Clear any existing animations on cardsWrapper
            gsap.killTweensOf(cardsWrapper.current);

            gsap.to(cardsWrapper.current, {
              x: -totalWidth - 200,
              ease: "none",
              scrollTrigger: {
                trigger: container.current,
                start: `top+=${120} top`,
                end: () => `+=${totalWidth}`,
                scrub: true,
                pin: true,
                invalidateOnRefresh: true,
                onUpdate: (self) => {
                  if (progressbar) {
                    progressbar.style.width = `${self.progress * 100}%`;
                  }
                },
                onRefresh: () => {
                  ScrollTrigger.update();
                },
              },
            });
          },

          "(max-width: 1024px)": function () {
            // Kill only ScrollTriggers related to this component
            ScrollTrigger.getAll().forEach((t) => {
              if (
                t.trigger === container.current ||
                (t.vars && t.vars.trigger === container.current)
              ) {
                t.kill();
              }
            });

            if (cardsWrapper.current) {
              gsap.set(cardsWrapper.current, { clearProps: "all" });
            }

            const progressbar = document.querySelector(".progress-bar");
            if (progressbar) {
              progressbar.style.width = "100%";
            }
          },
        });
      };

      // Use requestAnimationFrame for better timing with DOM rendering
      const rafId = requestAnimationFrame(() => {
        setupScrollTrigger();
      });

      return () => {
        cancelAnimationFrame(rafId);
      };
    },
    { scope: container, dependencies: [datas] },
  );

  useEffect(() => {
    const handleResize = () => {
      if (ScrollTrigger) {
        ScrollTrigger.refresh();
      }
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);

      // Cleanup ScrollTriggers
      ScrollTrigger.getAll().forEach((t) => {
        if (
          t.trigger === container.current ||
          (t.vars && t.vars.trigger === container.current)
        ) {
          t.kill();
        }
      });
    };
  }, []);

  return (
    <section className="home-wrapper-v2 item-start c-gap" ref={container}>
      <div className="home-process-header-v2 md-flex-column justify_between w-100 ">
        <div className="item-start gap-lg home-process-content-v2">
          <span className="lightcolor-badge-v2 about-p flex-center gap-2">
            <img src={process} alt="process-icon" />
            Our Process
          </span>
          <h2 className="about-h2 ">
            A Closer Look at Our Stepwise Development
          </h2>
        </div>
        <div className="buttons mt-5">
          <Link to="/contact-us" className="roll-button roll-btn">
            <span>View Live Demo</span>
          </Link>
          <TalkToUsNew
            db={db}
            label="Free Consultation"
            customClass="normal-roll-button normal-roll-btn border-0"
          />
        </div>
      </div>
      <p className="about-p light-gray">
        At our development company, innovation takes center stage. Our process
        is a carefully orchestrated symphony of creativity, collaboration, and
        precision. From conceptualization to execution, we navigate each project
        with strategic planning, cutting-edge technology, and a relentless
        pursuit of excellence. Embrace the future of development with us, where
        ideas transform into seamless solutions, and possibilities are
        limitless.
      </p>
      <div className="horizontal-scroll-section w-100">
        <div className="progress-container">
          <span className="progress-bar"></span>
        </div>

        <ul className="flex-center horizontal-scroll-cards" ref={cardsWrapper}>
          {datas.map((data, ind) => (
            <li
              key={data.id}
              className={`horizontal-scroll-card flex-column w-100 list-unstyled ${
                ind % 2 === 0 ? "" : "bg-add"
              }`}
            >
              <p className="about-h2 light-gray text-end">0{data.number}</p>
              <div className="horizontal-scroll-content item-start gap-lg">
                <h3 className="about-h4">{data.title}</h3>
                <p className="about-p-two">{data.desc}</p>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </section>
  );
};

export default Process;
