import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { Suspense } from "react";

import Seo from "@components/seo";
import Layout_v2 from "@components/layout_v2";
import HomeV3 from "@screens/home_v3";

const IndexPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const { data } = props;
  const fileUrl =
    data?.logo?.childImageSharp?.gatsbyImageData?.images?.fallback?.src;
  const validData = data?.allMetaJson?.nodes[0];

  return (
    <Suspense fallback={""}>
      <Seo
        title={validData?.title}
        metaKeyword={validData?.metaKeyword}
        metaTitle={validData?.metaTitle}
        description={validData?.description}
        image={fileUrl}
        siteFirestore={data?.site?.siteMetadata?.firestore}
        slug={"/"}
      ></Seo>
      <div className="landing_page-v2">
        <Layout_v2 db={db}>
          <HomeV3 db={db} />
        </Layout_v2>
      </div>
    </Suspense>
  );
};

export default IndexPage;

export const queryData = graphql`
  query MyQuery {
    logo: file(relativePath: { eq: "new_logo.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
    allMetaJson(filter: { slug: { eq: "/" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
