const TechStack = () => {
  const [activeCategory, setActiveCategory] = useState("web");

  const categories = Object.values(
    datas.reduce((acc, item) => {
      if (!acc[item.category]) {
        acc[item.category] = {
          key: item.category,
          catName: item.catName,
        };
      }
      return acc;
    }, {}),
  );

  const filteredData = datas.filter((data) => data.category === activeCategory);

  return (
    <section className="home-wrapper-v2 item-start c-gap" id="techstack">
      <div className="home-stack-heading-v2 w-100 ">
        <h3 className="about-h3">
          <span className="gradient-text-home">
            Specialized in 40+ Technologies
          </span>
        </h3>
      </div>
      <div className="home-stack-header-v2 item-center w-100 gap-md">
        <span className="lightcolor-badge-v2 about-p flex-center gap-2">
          <img src={tech_icon} alt="tech-icon" />
          Technology
        </span>
        <div className="item-center gap-3">
          <h3 className="about-h3">Our Tech Stack.</h3>
          <p className="about-p light-gray">Our Development Technologies</p>
        </div>
      </div>
      <div className="home-stacks-v2 w-100 item-center c-gap">
        <div className="gap-md home-stacks-category-btns col_flex">
          {categories.map((cat) => (
            <div key={cat.id} className="home-stacks-category-accord">
              <button
                key={cat.key}
                className={`home-stacks-category-btn about-p-two ${
                  activeCategory === cat.key
                    ? "home-stacks-category-btn-active"
                    : ""
                }`}
                onClick={() => setActiveCategory(cat.key)}
              >
                {cat.catName}
              </button>
              {activeCategory === cat.key && (
                <div className="accordion-modal-box ">
                  {filteredData.map((data) => (
                    <div
                      key={data.id}
                      className={`item-center gap-sm home-stacks-card `}
                    >
                      <div className="accordian-stact-image">
                        <img src={data.icon} alt={data.title} />
                      </div>
                      <p className="about-p-three fw-600 light-gray text-nowrap">
                        {data.title}
                      </p>
                    </div>
                  ))}
                </div>
              )}
            </div>
          ))}
        </div>
        <div className="gap-md home-stacks-cards col_flex">
          {filteredData.map((data) => (
            <div key={data.id} className="item-center gap-md home-stacks-card">
              <img src={data.icon} alt={data.title} />
              <p className="about-p-three fw-600 light-gray text-nowrap">
                {data.title}
              </p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default TechStack;
